
.btn_root-default, .btn_root-active {
  width: 120px;
  height: 40px;
  -webkit-border-radius: 4px;
          border-radius: 4px;
  border: solid 1px #0293dc;
  font-size: 16px;
  cursor: pointer;
}
.btn_root-default:hover, .btn_root-active {
  color: #fff;
  background-color: #0293dc;
}
.btn_root-default {
  color: #0293dc;
  background-color: #fff;
  -webkit-transition: all .3s;
  transition: all .3s;
}



