
.page_root {
  overflow: hidden;
  color: #808080;
}
.page_pageList {
  float: left;
  overflow: hidden;
}
.page_pagesItem {
  min-width: 36px;
  height: 28px;
  line-height: 28px;
  margin-right: 10px;
  padding: 0 4px;
  float: left;
  color: #3d3d3d;
  cursor: pointer;
  text-align: center;
  font-size: 12px;
  border: 1px solid #ddd;
  -webkit-border-radius: 2px;
          border-radius: 2px;
  -webkit-transition: all .3s;
  transition: all .3s;
  background-color: #fff;
}
.page_pagesItem--active, .page_pagesItem:hover {
    color: #fff;
    border-color: #0293dc;
    background-color: #0293dc;
}
.page_count {
  height: 30px;
  line-height: 30px;
  margin-right: 8px;
  float: left;
  font-size: 12px;
}
.page_to {
  height: 30px;
  float: left;
  overflow: hidden;
  font-size: 12px;
}
.page_toInput {
    height: 27px;
    width: 46px;
    margin: 0 4px;
    padding: 0 4px;
    color: #808080;
    text-align: center;
    border: 1px solid #ddd;
    -webkit-border-radius: 2px;
            border-radius: 2px;
    background-color: #fff;
}
.page_toBtn {
    height: 27px;
    width: 36px;
    color: #3d3d3d;
    cursor: pointer;
    font-size: 12px;
    border: 1px solid #D1D1D1;
    -webkit-border-radius: 2px;
            border-radius: 2px;
    background-color: #fff;
}



