
.date_root {
  position: relative;
  width: 120px;
  height: 36px;
  -webkit-border-radius: 2px;
          border-radius: 2px;
  border: 1px solid #2a7bea;
  color: #4c4c4c;
  font-size: 12px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.date_act {
  padding-left: 32px;
}
.date_current {
  position: relative;
  height: 100%;
  padding: 0 27px 0 10px;
  line-height: 36px;
}
.date_current em {
    position: absolute;
    width: 17px;
    height: 16px;
    margin-top: -8px;
    top: 50%;
    right: 6px;
    cursor: pointer;
    background: url("../../assets/imgs/calendar.png");
}
.date_calender {
  position: absolute;
  width: 216px;
  padding: 10px;
  top: 36px;
  z-index: 100;
  -webkit-box-shadow: 0 0 2px gray;
          box-shadow: 0 0 2px gray;
  -webkit-border-radius: 2px;
          border-radius: 2px;
  background-color: #fff;
}
.date_calender ul {
    overflow: hidden;
}
.date_calender li {
    float: left;
    text-align: center;
}
.date_pre, .date_next {
  margin: 0 2px;
  cursor: pointer;
}
.date_actYear {
  margin: 0 12px;
  cursor: pointer;
}
.date_actMonth, .date_actDay {
  margin: 0 5px;
  cursor: pointer;
}
.date_clear {
  margin: 0 8px;
  cursor: pointer;
}
.date_week, .date_day, .date_day-space, .date_day-active, .date_day-disable {
  width: 26px;
  height: 26px;
  margin: 1px;
  line-height: 28px;
}
.date_day:hover, .date_month:hover, .date_year:hover, .date_day-active, .date_month-active, .date_year-active {
  color: #fff;
  font-weight: bold;
  background-color: #ccc;
}
.date_day, .date_month, .date_year {
  cursor: pointer;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.date_day-disable, .date_month-disable, .date_year-disable {
  color: #666;
  cursor: no-drop;
  background-color: #e3e3e3;
}
.date_month, .date_month-active, .date_month-disable {
  width: 47px;
  line-height: 47px;
  margin: 1px;
}
.date_year, .date_year-active, .date_year-disable {
  width: 63.333px;
  line-height: 63.333px;
  margin: 1px;
}



