
@charset "UTF-8";
.nav_root {
  width: 250px;
  height: 100%;
  background-color: #fff;
}
.nav_item {
  font-size: 16px;
  color: #718e9c;
}
.nav_item:nth-of-type(1) .nav_title::after {
  content: "";
  position: absolute;
  top: 0;
  left: 50px;
  font-size: 20px;
  font-family: iconfont;
}
.nav_item:nth-of-type(2) .nav_title::after {
  content: "";
  position: absolute;
  top: 0;
  left: 50px;
  font-size: 20px;
  font-family: iconfont;
}
.nav_item:nth-of-type(3) .nav_title::after {
  content: "";
  position: absolute;
  top: 0;
  left: 50px;
  font-size: 20px;
  font-family: iconfont;
}
.nav_title:hover, .nav_title-active, .nav_listItem:hover, .nav_listItem-active {
  position: relative;
  color: #0293dc;
  background-color: #d9f0fc;
}
.nav_title:hover::before, .nav_title-active::before, .nav_listItem:hover::before, .nav_listItem-active::before {
    content: '';
    position: absolute;
    left: 0;
    width: 2px;
    height: 100%;
    background-color: #0293dc;
}
.nav_title {
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
  height: 60px;
  cursor: pointer;
  line-height: 60px;
  padding: 0 34px 0 78px;
  background-repeat: no-repeat;
  background-position: 38px center;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.nav_listItem {
  height: 48px;
  line-height: 48px;
  padding-left: 78px;
  cursor: pointer;
}



